___CSS_LOADER_EXPORT___.push([module.id, `.sash-wrap-line {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 1s;
}
.sash-wrap-line .line {
  display: block;
  height: 100%;
  width: 1px;
  background-color: rgba(0, 0, 0, .2);
}
.sash-wrap-line.active {
  background-color: lightgray;
}
.sash-wrap-line.inactive {
  transition: none;
}

.action-sash-wrap {
  width: 1px;
  position: relative;
  height: 100%;
  display: flex;
  align-items: start;
  justify-content: center;
  background-color: rgba(0, 0, 0, .1);
}

.action-sash-wrap.pane-horizontal {
  flex-direction: column;
}


.action-sash-wrap .action:hover {
  color: rgba(0, 0, 0, .8);
}

.action-sash-wrap .action {
  cursor: pointer;
  width: 15px;
  margin-right: 15px;
  z-index: 2;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  color: rgba(0, 0, 0, .5);
  font-size: 8px;
}

.action-sash-wrap.pane-mainFirst .action {
  margin-left: 30px;
}

.action-sash-wrap.pane-horizontal {
  width: 100%;
  height: 1px;
  align-items: end;
}

.action-sash-wrap.pane-horizontal .action {
  height: 15px;
  width: 20px;
  margin-top: 15px;
  margin-left: 0px;
  margin-right: 0px;
  align-items: baseline;
}

html, body {
  margin: 0px;
  padding: 0px;
}`, "",{"version":3,"sources":["webpack://./src/lib/components/style.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,kBAAkB;AACpB;AACA;EACE,cAAc;EACd,YAAY;EACZ,UAAU;EACV,mCAAmC;AACrC;AACA;EACE,2BAA2B;AAC7B;AACA;EACE,gBAAgB;AAClB;;AAEA;EACE,UAAU;EACV,kBAAkB;EAClB,YAAY;EACZ,aAAa;EACb,kBAAkB;EAClB,uBAAuB;EACvB,mCAAmC;AACrC;;AAEA;EACE,sBAAsB;AACxB;;;AAGA;EACE,wBAAwB;AAC1B;;AAEA;EACE,eAAe;EACf,WAAW;EACX,kBAAkB;EAClB,UAAU;EACV,kBAAkB;EAClB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,YAAY;EACZ,wBAAwB;EACxB,cAAc;AAChB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,WAAW;EACX,gBAAgB;AAClB;;AAEA;EACE,YAAY;EACZ,WAAW;EACX,gBAAgB;EAChB,gBAAgB;EAChB,iBAAiB;EACjB,qBAAqB;AACvB;;AAEA;EACE,WAAW;EACX,YAAY;AACd","sourcesContent":[".sash-wrap-line {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 1s;\n}\n.sash-wrap-line .line {\n  display: block;\n  height: 100%;\n  width: 1px;\n  background-color: rgba(0, 0, 0, .2);\n}\n.sash-wrap-line.active {\n  background-color: lightgray;\n}\n.sash-wrap-line.inactive {\n  transition: none;\n}\n\n.action-sash-wrap {\n  width: 1px;\n  position: relative;\n  height: 100%;\n  display: flex;\n  align-items: start;\n  justify-content: center;\n  background-color: rgba(0, 0, 0, .1);\n}\n\n.action-sash-wrap.pane-horizontal {\n  flex-direction: column;\n}\n\n\n.action-sash-wrap .action:hover {\n  color: rgba(0, 0, 0, .8);\n}\n\n.action-sash-wrap .action {\n  cursor: pointer;\n  width: 15px;\n  margin-right: 15px;\n  z-index: 2;\n  position: absolute;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  height: 20px;\n  color: rgba(0, 0, 0, .5);\n  font-size: 8px;\n}\n\n.action-sash-wrap.pane-mainFirst .action {\n  margin-left: 30px;\n}\n\n.action-sash-wrap.pane-horizontal {\n  width: 100%;\n  height: 1px;\n  align-items: end;\n}\n\n.action-sash-wrap.pane-horizontal .action {\n  height: 15px;\n  width: 20px;\n  margin-top: 15px;\n  margin-left: 0px;\n  margin-right: 0px;\n  align-it